<template>
	<div>
		<headerNav title="修改地址"/>
		<van-address-edit
				:area-list="areaList"
				:showDelete="showDelete"
				:addressInfo="info"
				@save="onSave"
				@delete="onDelete"
		/>
	</div>
</template>

<script>
	import {AddressEdit, Dialog} from 'vant';

	import headerNav from '../../../components/header/nav'
	import areaList from '../../../utils/area';
	import {mapState} from 'vuex';

	export default {
		name: 'AddressDetail',

		components: {
			headerNav,
			AddressEdit,
		},

		data() {
			return {
				areaList,
				showDelete: false,
				info: {},
				index: undefined,
			}
		},

		computed: {
			...mapState(['address'])
		},

		created() {
			this.index = this.$route.query.index;
			this.showDelete = this.index !== undefined;
			if (this.showDelete) {
				let tmp = this.address[this.index].address.split(' ');
				this.info = {
					id: this.address[this.index].AddressID,
					name: this.address[this.index].name,
					tel: this.address[this.index].tel,
					addressDetail: tmp[tmp.length - 1],
					areaCode: this.getAreaCode(tmp[0], tmp[1], tmp[2]),
				}
			}
		},

		methods: {
			getAreaCode(province, city, county) {
				for (let key of Object.keys(areaList.county_list)) {
					if (areaList.county_list[key] === county) {
						return key;
					}
				}
				for (let key of Object.keys(areaList.city_list)) {
					if (areaList.city_list[key] === city) {
						return key;
					}
				}
				for (let key of Object.keys(areaList.province_list)) {
					if (areaList.province_list[key] === province) {
						return key;
					}
				}
				return '';
			},

			async onSave(data) {
				let area = `${data.province} ${data.city} ${data.county}`;
				if (this.index !== undefined) {
					let res = await this.$request.updateAddress(this.address[this.index].AddressID, data.name, data.tel, area, data.addressDetail.trim());
					if (res) {
						this.$Message.success('修改成功');
						this.$router.go(-1);
					}
				} else {
					let res = await this.$request.addAddress(data.name, data.tel, area, data.addressDetail);
					if (res) {
						this.$Message.success('保存成功');
						this.$router.go(-1);
					}
				}
			},

			async onDelete(data) {
				let res = await this.$request.deleteAddress(this.address[this.index].AddressID);
				if (res) {
					this.$Message.info('删除成功');
					this.$router.go(-1);
				}
			},
		},
	}
</script>

<style>
	.van-picker__toolbar {
		font-size: 16px;
	}
</style>



